.searchHeader {
   top: 0;
   left: 0;
   right: 0;
   position: sticky;
   z-index: 1100;
   display: flex;
   justify-content: center;
   align-items: center;
   min-height: 100px;
   padding-right: 24px;
   padding-left: 24px;
   color: #fff;
   background: linear-gradient(270deg, #f42f03, #0363f4);
   box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.5),
      0px 4px 5px 0px rgba(0, 0, 0, 0.16), 0px 1px 10px 0px rgba(0, 0, 0, 0.19);
}

// ================= PIXABEY_lOGO =====================
.pixabay__logo_link {
   margin-left: 100px;
   opacity: 0.7;
   transform: scale(1);

   transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1),
      opacity 350ms cubic-bezier(0.4, 0, 0.2, 1),
      box-shadow 350ms cubic-bezier(0.4, 0, 0.2, 1);

   &:hover,
   &:focus {
      transform: scale(1.5);
      opacity: 1;
      animation: pixabay__logo_link-spin 0s cubic-bezier(0.4, 0, 0.2, 1);
      box-shadow: 0px 2px 4px 2px #f42f03, 0px 3px 5px 4px #0363f4,
         0px 3px 6px 5px #f42f03;
   }
}
.pixabay__logo_img {
   max-width: 55px;
   height: auto;
}
@media (prefers-reduced-motion: no-preference) {
   .pixabay__logo_link {
      animation: pixabay__logo_link-spin infinite 4s linear;
   }
}
@keyframes pixabay__logo_link-spin {
   from {
      transform: rotateY(0deg);
   }
   to {
      transform: rotateY(360deg);
   }
}

// ================= SEARCH_FORM =====================
.searchForm {
   box-sizing: border-box;
   position: relative;
   display: flex;
   align-items: center;
   width: 100%;
   max-width: 600px;
   margin-right: auto;
   margin-left: auto;
   background-color: #fff;
   overflow: hidden;
   z-index: 10;
   box-shadow: 0px 9px 10px 0.8px #2670df, 1px 15px 20px 0.8px #ff3204;
   border-radius: 5px;
}

// ================= SEARCH_INPUT =====================
.searchForm__input {
   box-sizing: border-box;
   width: 100%;
   min-height: 40px;
   padding-top: 8px;
   padding-left: 10px;
   font-size: 17px;
   color: #f42f03;
   z-index: 1150;
   border: 0;
   outline: 0;
   opacity: 0.8;

   transition: opacity 350ms cubic-bezier(0.4, 0, 0.2, 1),
      background-color 350ms cubic-bezier(0.4, 0, 0.2, 1);

   &:hover {
      opacity: 1;
      background-color: rgba(244, 241, 241, 0.578);
   }
}

// ================= SEARCH_LABEL =====================
.placeholder {
   position: absolute;
   top: 50%;
   left: 50%;
   pointer-events: none;
   color: #f47703;
   z-index: 1200;
   transform: translate(-130%, -50%);
   transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1),
      color 350ms cubic-bezier(0.4, 0, 0.2, 1);
}

.searchForm__input:focus ~ .placeholder,
.searchForm__input:not(:placeholder-shown) ~ .placeholder {
   transform: translateY(-21px) translateX(-140%) scale(0.75);
}

.searchForm__input:not(:placeholder-shown) ~ .placeholder {
   color: #0363f4;
   transform: translateY(-21px) translateX(-50%) scale(0.75);
}

.searchForm__input:focus ~ .placeholder {
   color: #f42f03;
}

// ================= SEARCH_BUTTON =====================
.searchForm__button {
   display: inline-block;
   padding: 10px 14px;
   height: 40px;
   opacity: 0.7;
   outline: none;
   color: #f42f03;
   background-color: rgba(210, 207, 207, 0.776);
   text-decoration: none;
   border: none;
   border-radius: 3px;
   cursor: pointer;
   letter-spacing: 3px;
   z-index: 1200;
   transform: scale(1);

   transition: color 340ms cubic-bezier(0.4, 0, 0.2, 1),
      background-color 350ms cubic-bezier(0.4, 0, 0.2, 1),
      box-shadow 350ms cubic-bezier(0.4, 0, 0.2, 1),
      border-radius 350ms cubic-bezier(0.4, 0, 0.2, 1),
      opacity 350ms cubic-bezier(0.4, 0, 0.2, 1),
      transform 350ms cubic-bezier(0.4, 0, 0.2, 1);

   &:hover,
   &:focus {
      transform: scale(1.15);
      background-color: #0363f4;
      color: rgba(210, 207, 207, 0.776);
      border: none;
      opacity: 1;
      border-radius: 4px;
      box-shadow: 0px 2px 8px #f42f03, 0px 3px 19px #f42f03,
         0px 5px 25px #f42f03, 2px 5px 30px #f42f03;
   }
}
.searchForm__button_label {
   position: absolute;
   width: 1px;
   height: 1px;
   padding: 0;
   overflow: hidden;
   clip: rect(0, 0, 0, 0);
   white-space: nowrap;
   clip-path: inset(50%);
   border: 0;
}
